::selection {
  color: white;
  background-color: color($pink, 400);
}
html,
body {
  background-color: $color-bg;
}
.logo {
  @include span-columns(2);
}
.logo--mark {
  @include soft-transition();
  fill: white;
}

// Main Blocks
.main,
header {
  @include outer-container();
  @include pad();
}
.main {
  padding-top: 0;
  min-height: 65vh;
}

// Navigation
header:hover {
  .logo--mark--quotes {
    @include soft-transition();
    @include animation(quotes-spin 2.0s linear);
    @include animation-iteration-count(infinite);
    transform-origin: 50% 50%;
    fill: $color-strong;
  }
}
  nav ul {
    float: right;
  }
  nav li {
    display: inline;
    margin-right: 10px;
  }
    nav li a.active {
      border-bottom: 2px dotted $color-main;
      padding-bottom: .5em;
    }

// Buttons
.btn {
  // border: 3px solid $color-main;
  @include soft-transition();
  // background: color($pink, a400);
  border: 3px solid $color-accent;
  padding: .3em .5em .35em;
  border-radius: 0.3em;

  font-size: 1.5em;
  color: $color-main;
  white-space:nowrap;

  &:hover {
    background: $color-accent;
    // background: white;
    // color: color($pink, a400);
    color: white;
  }
}

// Typography
h1 {
  font-size: 4em;
  font-weight: 900;
  // text-transform: uppercase;
}
h2 {
  margin-bottom: .15em;
  margin-top: 0;
  font-size: 2em;
  line-height: 1em;
}
h3 {
  font-size: 2em;
}
h4 {
  font-size: 1.6em;
  margin-bottom: 0;
}
