.intro {
  @include span-columns(10);
  @include shift();
}
  .intro__opening {
    font-weight: 400;
    font-size: 3em;
    line-height: 1.1;
    margin-top: 7vh;

    em {
      font-weight: 200;
      font-style: normal;

      // color: $color-strong;
    }

    strong {
      font-weight: 900;
      // color: $color-strong;
    }
  }

.event {
  @include span-columns(10);
  @include shift();
  // @include pad(.8em 1.2em 1.6em);
  margin-top: 8vh;
  margin-bottom: 8vh;
  // background: color($grey, 900)
  text-align: center;
}
  .event__date {
    font-size: 1.3em;
    line-height: 1.1em;
    font-weight: 600;
    color: $color-secondary;
  }
  .event__callout a {
    // font-weight: bold;
    padding-bottom: .2em;
    border-bottom: 1px solid $color-secondary;
  }

.home__callout {
  @include span-columns(6);
  @include shift(3);
  text-align: center;

  a {
    width: 80%;
    margin: 0 auto;
    display: block;
    margin-bottom: $base-spacing;
  }
}
