footer {
  @include pad();
  @include clearfix;
  margin-top: 15vh;
  width: 100%;
  background: $color-accent;
  color: $color-secondary;
  height: auto;
  font-size: .8em;

    section,
    .footer__img {
      @include span-columns(8);
      @include shift(2);
      @include row();
      margin-top: $base-spacing*2;
    }

    a {
      @include soft-transition();
      color: $color-secondary;
      border-bottom: 1px dotted $color-secondary-soft;
      padding-bottom: .2em;

      &:hover {
        background-color: $color-main;
        border-bottom-color: transparentize($color-accent, 1);
        color: $color-accent;
        padding: .4em;
      }
    }
}
  #footer__logo {
    margin-top: -$base-spacing*9;
    z-index: -1;

    g {
      @include soft-transition();
      fill: $color-secondary;
    }
    &:hover {
      .quotes {
        @include soft-transition();
        @include animation(quotes-spin 3.0s linear);
        @include animation-iteration-count(infinite);
        transform-origin: 50% 50%;
        fill: $color-strong;
      }
      .light {
        fill: white;
      }
    }
  }

.footer__info {
  @include span-columns(10);
  @include shift(1);
  text-align: center;

  h4 {
    font-weight: 400;
    font-size: .8em;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 600;
    margin-bottom: $base-spacing/1.8;
  }
}
  .footer__info--contact {
    @include span-columns(5 of 10);
    @include pad();
    border-right: 1px solid $color-secondary;
  }
  .footer__info--social {
    @include span-columns(5 of 10);
    @include pad();
    a {
      border-bottom: none;
      width: 40%;
      margin: 0 auto;

      &:hover {
        border-bottom: none;
        background: transparent;
        padding: 0;

        svg {
          @include animation(quotes-spin .6s linear);
          @include animation-iteration-count(infinite);
        }
      }

      svg {
        fill: $color-secondary;
        width: 30px;
        &:first-child {
          margin-right: 1em;
        }
      }
    }
  }
.footer__copyright {
  text-align: center;
  p:first-child {
    font-weight: 600;
    margin-bottom: 0;
  }
}
.designby {
  font-size: .65em;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase;
  opacity: .64;
  text-align: center;
  color: $color-secondary;

  a {
    &:hover {
      color: $color-bg;
      border-bottom-color: transparentize($color-bg, 1);
    }
  }
}
